import {
	type TokenResponse,
	googleLogout,
	useGoogleLogin,
} from "@react-oauth/google";
import React, { useState } from "react";
import { SpreadsheetData } from "./SpreadsheetData.tsx";

export const GoogleAuth = () => {
	const [user, setUser] = useState<Omit<TokenResponse, "error">>(() =>
		JSON.parse(localStorage.getItem("googleUser") || "null"),
	);

	const login = useGoogleLogin({
		onSuccess: (codeResponse) => {
			setUser(codeResponse);
			localStorage.setItem("googleUser", JSON.stringify(codeResponse));
		},
		onError: (error) => console.log("Login Failed:", error),
		scope: "https://www.googleapis.com/auth/spreadsheets",
	});

	const handleLogout = () => {
		googleLogout();
		localStorage.removeItem("googleUser");
		setUser(undefined);
	};
	return (
		<div>
			{user?.access_token ? (
				<>
					<SpreadsheetData
						accessToken={user.access_token}
						spreadsheetId="1J5T_Tq1iViHlLedZnPKwFByglCDqffdhuMFu3J8eIzA"
						logout={handleLogout}
					/>
				</>
			) : (
				<div className="flex items-center justify-center h-screen">
					<button
						class="bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
						onClick={login}
					>
						Sign in with Google {" "}
					</button>
				</div>
			)}
		</div>
	);
};
